<template>
    <div class="workers-component">
        <h1 class="main-header">Produkty i usługi</h1>
        <div class="cards-row">
            <div class="card-3">
                <h1 class="card-title">Tworzenie produktu</h1>
                <div class="card-content">
                    <form class="add-product-form" @submit.prevent="addProduct">
                        <div class="form-cols">
                            <div class="form-col">
                                <md-field>
                                    <label>Nazwa produktu</label>
                                    <md-input required v-model="product.name"></md-input>
                                </md-field>
                                <md-field>
                                    <label for="type">Kategoria produktu</label>
                                    <md-select required v-model="product.category" name="type" id="type">
                                        <md-option value="restaurant">Gastronomia</md-option>
                                        <md-option value="bar">Bar</md-option>
                                        <md-option value="wellness">Wellness & SPA</md-option>
                                    </md-select>
                                </md-field>
                            </div>
                            <div class="form-col">
                                <md-field>
                                    <label>Cena</label>
                                    <md-input required type="number" v-model="product.cost"></md-input>
                                </md-field>
                                <md-field>
                                    <label for="type">Dostępność</label>
                                    <md-select required v-model="product.availability" name="availability" id="availability">
                                        <md-option value="true">Tak</md-option>
                                        <md-option value="false">Nie</md-option>
                                    </md-select>
                                </md-field>
                            </div>
                        </div>
                        <button type="submit">Dodaj produkt</button>
                    </form>
                </div>
            </div>
            <div class="card-1">
                <h1 class="card-title">Zysk z usług i produktów</h1>
                <div class="card-content">
                    <div class="profit-box gold">
                        <p>Gastronomia</p>
                        <div class="profit">
                            <span>{{profitRestaurant}} PLN</span>
                        </div>
                    </div>
                    <div class="profit-box purple">
                        <p>Bar</p>
                        <div class="profit">
                            <span>{{profitBar}} PLN</span>
                        </div>
                    </div>
                    <div class="profit-box blue">
                        <p>Wellness</p>
                        <div class="profit">
                            <span>{{profitWellness}} PLN</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="cards-row">
            <div class="card-3">
                <h1 class="card-title">Lista produktów</h1>
                <div class="card-content">
                    <div class="products-list">
                        <div class="list-header">
                            <p class="list-col-25">Nazwa</p>
                            <p class="list-col-25">Kategoria</p>
                            <p class="list-col-15">Cena</p>
                            <p class="list-col-20">Dostępność</p>
                        </div>
                        <div v-for="product in products" class="product-row" :key="product._id">
                            <p class="list-col-25">{{product.name}}</p>
                            <p class="list-col-25">{{product.category | categoryFilter}}</p>
                            <p class="list-col-15">{{product.cost}} PLN</p>
                            <p class="list-col-20">{{product.availability | availableFilter}}</p>
                            <div class="product-delete list-col-15">
                                <button @click="deleteProduct(product._id)">Usuń</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import axios from 'axios';
    export default {
        data() {
            return {
                product: {
                    name: '',
                    category: '',
                    cost: 0,
                    availability: true
                },
                profitRestaurant: 0,
                profitBar: 0,
                profitWellness: 0,
                products: []
            }
        },
        beforeMount() {
            this.getProducts();
        },
        methods: {
            getProducts() {
                axios.get('/get-products')
                    .then(res => {
                        /* eslint-disable */
                        console.log(res.data.products);
                        const products = res.data.products;
                        this.products = products;
                        products.forEach(product => {
                            if (product.category == 'restaurant') {
                                this.profitRestaurant +=  product.cost;
                            } else if (product.category == 'bar') {
                                this.profitBar +=  product.cost;
                            } else if (product.category == 'wellness') {
                                this.profitWellness += product.cost;
                            }
                        })
                    })
            },
            deleteProduct(id) {
                axios.post('/delete-product', {
                        id: id
                    })
                    .then(res => {
                        this.getProducts();
                    })
            },
            addProduct() {
                axios.post('/add-product', {
                        product: this.product
                    })
                    .then(res => {
                        this.getProducts();
                    })
            }
        },
        filters: {
            categoryFilter(category) {
                if (category == 'restaurant') {
                    return "Gastronomia";
                } else if (category == 'bar') {
                    return "Bar";
                } else if (category == 'wellness') {
                    return "Wellness & SPA";
                }
            },
            availableFilter(available) {
                if (available == true) {
                    return "Dostępny";
                } else if (available == false) {
                    return "Niedostępny";
                }
            }
        },
    }
</script>